<template>
  <v-container class="coin" fluid fill-height>
    <v-layout row align-center wrap>
      <v-flex xs12 sm12 md8>
        <template v-if="loading === false">
          <tradingview
          :coin="$route.params.coin"
          :period="$route.params.period"
          ></tradingview>
        </template>
        <template v-else>
          <div class="grafbox-center">
            <v-progress-circular
              :size="50"
              color="white"
              indeterminate
              ></v-progress-circular>
          </div>
        </template>
      </v-flex>
      <v-flex xs12 sm12 md4>
        <div class="text-xs-center">
          <v-btn fab dark :small="this.$route.params.period !== 'M5'" color="primary" @click="toCoinTime('M5')">
            M5
          </v-btn>
          <v-btn fab dark :small="this.$route.params.period !== 'H1'" color="primary" @click="toCoinTime('H1')">
            H1<br>
          </v-btn>
          <v-btn fab dark :small="this.$route.params.period !== 'H4'" color="primary" @click="toCoinTime('H4')">
            H4
          </v-btn>
          <v-btn fab dark :small="this.$route.params.period !== 'D1'" color="primary" @click="toCoinTime('D1')">
            D1
          </v-btn>
        </div>
        <div class="text-xs-center tahometer">
          <v-progress-circular
            :rotate="360"
            :size="200"
            :width="15"
            :value="coin.tahometer_percent"
            :color="getColor"
            >
            <template v-if="coin.tahometer_percent !== 0">
              {{ coin.tahometer_percent }}<br>
              {{ recomendation }}
            </template>
            <template v-else>
              NO DATA
            </template>
          </v-progress-circular>
        </div>
        <template v-if="coin">
          <div class="text-xs-center mt-5 pa-4">
            <v-card>
              <v-card-title class="text-xs-center">
                <p class="p_full-width">{{ coin.name }}</p>
                <v-chip>Ma(14): <span class="ml-1" v-html="ma_14"></span></v-chip>
                <v-chip>Ma(55): <span class="ml-1" v-html="ma_55"></span></v-chip>
                <v-chip>Ma(120): <span class="ml-1" v-html="ma_120"></span></v-chip>
                <v-chip>Ma(240): <span class="ml-1" v-html="ma_240"></span></v-chip>
                <v-chip>Bollinger Bands: <span class="ml-1" v-html="bb"></span></v-chip>
                <v-chip>ATR(14): <span class="ml-1" v-html="atr"></span></v-chip>
                <v-chip>Donchian Channel: <span class="ml-1" v-html="dc"></span></v-chip>
                <v-chip>Bull Bear Power: <span class="ml-1" v-html="bbp"></span></v-chip>
                <v-chip>Average Directional Index: <span class="ml-1" v-html="adx"></span></v-chip>
                <v-chip>Relative Strength Index: <span class="ml-1" v-html="rsi"></span></v-chip>
                <v-chip>MACD: <span class="ml-1" v-html="macd"></span></v-chip>
                <v-chip>Stochastic Oscillator: <span class="ml-1" v-html="so"></span></v-chip>
                <v-chip>Awesome Oscillator: <span class="ml-1" v-html="ao"></span></v-chip>
              </v-card-title>
            </v-card>
          </div>
        </template>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
// @ is an alias to /src
import TradingView from '../components/TradingView.vue'
export default {
  name: 'coin',
  components: {
    'tradingview': TradingView
  },
  data () {
    return {
      loading: true,
      loading_data: false,
      coin: false
    }
  },
  computed: {
    ma_14 () {
      const v = this.coin.lwmas[0] === null ? false : this.coin.lwmas[0]
      if (v === false) {
        return 'N/D'
      } else if (v.last_candle <= v.val) {
        return `<span class="red-text">sell</span>`
      } else if (v.last_candle > v.val) {
        return `<span class="green-text">buy</span>`
      } else {
        return 'N/D'
      }
    },
    ma_55 () {
      const v = this.coin.lwmas[1] === null ? false : this.coin.lwmas[1]
      if (v === false) {
        return 'N/D'
      } else if (v.last_candle <= v.val) {
        return `<span class="green-text">buy</span>`
      } else if (v.last_candle > v.val) {
        return `<span class="red-text">sell</span>`
      } else {
        return 'N/D'
      }
    },
    ma_120 () {
      const v = this.coin.lwmas[2] === null ? false : this.coin.lwmas[2]
      if (v === false) {
        return 'N/D'
      } else if (v.last_candle <= v.val) {
        return `<span class="green-text">buy</span>`
      } else if (v.last_candle > v.val) {
        return `<span class="red-text">sell</span>`
      } else {
        return 'N/D'
      }
    },
    ma_240 () {
      const v = this.coin.lwmas[3] === null ? 'false' : this.coin.lwmas[3]
      if (v === false) {
        return 'N/D'
      } else if (v.last_candle <= v.val) {
        return `<span class="green-text">buy</span>`
      } else if (v.last_candle > v.val) {
        return `<span class="red-text">sell</span>`
      } else {
        return 'N/D'
      }
    },
    bb () {
      const v = this.coin.bb.up === null || this.coin.bb.down === null ? false : this.coin.bb
      if (v === false) {
        return 'N/D'
      } else {
        return `<span class="green-text">buy</span>`
      }
    },
    atr () {
      return `<span class="green-text">buy</span>`
    },
    bbp () {
      return `<span class="green-text">buy</span>`
    },
    rsi () {
      return `<span class="green-text">buy</span>`
    },
    ao () {
      return `<span class="green-text">buy</span>`
    },
    so () {
      return `<span class="green-text">buy</span>`
    },
    dc () {
      return `<span class="green-text">buy</span>`
    },
    macd () {
      return `<span class="green-text">buy</span>`
    },
    adx () {
      return `<span class="green-text">buy</span>`
    },
    recomendation () {
      if (this.coin === false) {
        return 'sell'
      } else {
        const val = this.coin.tahometer_percent
        if (val <= 20) {
          return 'Strong Sell'
        } else if (val <= 40) {
          return 'Sell'
        } else if (val <= 60) {
          return 'Neutral'
        } else if (val <= 70) {
          return 'Buy'
        } else if (val <= 100) {
          return 'Strong Buy'
        }
        return 'white'
      }
    },
    getColor () {
      if (this.coin === false) {
        return 'red'
      } else {
        const val = this.coin.tahometer_percent
        if (val <= 40) {
          return 'red'
        } else if (val <= 60) {
          return 'white'
        } else if (val <= 100) {
          return 'blue'
        }
        return 'white'
      }
    }
  },
  methods: {
    toCoinTime (period) {
      return this.$router.push({
        name: 'coin',
        params: {
          coin: this.$route.params.coin,
          period: period
        }
      })
    },
    setData (to) {
      console.log(to)
      this.getData(to.params.coin, to.params.period)
    },
    getData (coin, period) {
      this.$api.get('/quote/' + coin + '/' + period).then((res) => {
        const data = res.data.data
        console.log(data)
        this.coin = data.coin
        this.$store.commit('set', { type: 'errorAlert', data: { type: 'success', text: 'Data updated' } })
      }).catch((e) => {
        this.$store.commit('set', { type: 'errorAlert', data: { type: 'error', text: e } })
      })
    }
  },
  created () {
  },
  mounted () {
    console.log('coin mounted')
    this.getData(this.$route.params.coin, this.$route.params.period)
    const timer = setTimeout(() => {
      this.loading = false
      clearTimeout(timer)
    }, 2000)
  },
  beforeRouteUpdate (to, from, next) {
    console.log('route updated')
    this.loading = true
    const timer = setTimeout(() => {
      this.loading = false
      clearTimeout(timer)
    }, 2000)
    this.setData(to)
    next()
  }
}
</script>
