<template>
  <v-container class="home" fluid fill-height>
    <v-layout align-center column>
      <v-flex xs12>
        <div class="text-xs-center">
          <v-btn fab dark :small="active !== 'M15'" color="primary" @click="toCorTime('M15')">
            M15
          </v-btn>
          <v-btn fab dark :small="active !== 'M30'" color="primary" @click="toCorTime('M30')">
            M30
          </v-btn>
          <v-btn fab dark :small="active !== 'H1'" color="primary" @click="toCorTime('H1')">
            H1<br>
          </v-btn>
          <v-btn fab dark :small="active !== 'H4'" color="primary" @click="toCorTime('H4')">
            H4
          </v-btn>
          <v-btn fab dark :small="active !== 'D1'" color="primary" @click="toCorTime('D1')">
            D1
          </v-btn>
        </div>
        <div class="text-xs-center">
          <v-btn fab dark :small="active_num !== 7" color="primary" @click="toCorNum(7)">
            7
          </v-btn>
          <v-btn fab dark :small="active_num !== 14" color="primary" @click="toCorNum(14)">
            14
          </v-btn>
          <v-btn fab dark :small="active_num !== 30" color="primary" @click="toCorNum(30)">
            30
          </v-btn>
        </div>
      </v-flex>
      <v-flex xs12>
        <v-data-table
          :headers="headers"
          :items="correlations"
          :loading="loading"
          hide-actions
          :disable-initial-sort="true"
          :rows-per-page-items="[14]"
          class="coins-table scrollbar"
          >
          <template v-slot:headers="props">
            <tr>
              <th>coins/coins</th>
              <th :key="key" v-for="(e, key) in props.headers ">
                {{e.name}}
              </th>
            </tr>
          </template>
          <template v-slot:items="props">
            <tr>
              <td>{{ coins[props.index].name }}</td>
              <td :key="key" v-for="(e, key) in props.item ">
                {{e}}
              </td>
            </tr>
          </template>
        </v-data-table>

      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
// @ is an alias to /src

export default {
  name: 'correlations',
  data () {
    return {
      loading: false,
      coins: [],
      headers: [],
      correlations: [],
      active: 'M15',
      active_num: 14
    }
  },
  methods: {
    toCorTime (period) {
      this.active = period
      this.getData()
    },
    toCorNum (number) {
      this.active_num = number
      this.getData()
    },
    setMainInfo () {
      this.$api.get('/correlations/M15/14').then((res) => {
        const data = res.data.data
        this.coins = data.crypto_currencies
        this.headers = data.crypto_currencies
        this.correlations = data.correlation_matrix
        console.log(data)
        this.loading = false
        this.$store.commit('set', { type: 'errorAlert', data: { type: 'success', text: 'Data updated' } })
      }).catch((e) => {
        this.$store.commit('set', { type: 'errorAlert', data: { type: 'error', text: e } })
      })
    },
    getData () {
      this.correlations = []
      this.loading = true

      this.$api.get('/correlations/' + this.active + '/' + this.active_num).then((res) => {
        const data = res.data.data
        this.correlations = data.correlation_matrix
        this.loading = false
      }).catch((e) => {
        this.$store.commit('set', { type: 'errorAlert', data: { type: 'error', text: e } })
      })
    }
  },
  created () {
    this.setMainInfo()
  },
  mounted () {
    let tableOverflow = document.getElementsByClassName('v-table__overflow')
    tableOverflow[0].classList.add('scrollbar')
  }
}
</script>
