          name: 'DASH'
        },
        {
          id: 13,
          name: 'XMR'
        },
        {
          id: 14,
          name: 'TRX'
        }
      ]
    }
  },
  methods: {
    goToCoin (coin) {
      return this.$router.push({
        name: 'coin',
        params: { coin: coin, period: 'M5' }
      })
    },
    toMain () {
      return this.$router.push({
        name: 'home'
      })
    },
    toCorrelations () {
      return this.$router.push({
        name: 'correlations',
        params: { time: 'M5', number: 14 }
      })
    }
  },
  created () {
    const tradingView = document.createElement('script')
    tradingView.setAttribute('src', 'https://s3.tradingview.com/tv.js')
    tradingView.setAttribute('type', 'text/javascript')
    document.getElementsByTagName('body')[0].appendChild(tradingView)
  }
}
</script>

