import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import Vuetify from 'vuetify'
import axios from './axios'

Vue.use(Vuetify)
Vue.prototype.$api = axios
Vue.config.productiontip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
