        { id: 14, text: 'Awesome Oscillator', sortable: false }
      ]
    }
  },
  methods: {
    getData (period) {
      this.$api.get('/quotes/' + period).then((res) => {
        const data = res.data.data
        this.coins = data.crypto_currencies
        console.log(this.coins)
        this.loading = false
        this.$store.commit('set', { type: 'errorAlert', data: { type: 'success', text: 'Data updated' } })
      }).catch((e) => {
        this.$store.commit('set', { type: 'errorAlert', data: { type: 'error', text: e } })
      })
    },
    toQuotes (period) {
      return this.$router.push({
        name: 'quotes',
        params: { period: period }
      })
    }
  },
  created () {
    console.log('created')
    this.getData('M5')
  },
  mounted () {
    console.log('mounted')
    let tableOverflow = document.getElementsByClassName('v-table__overflow')
    tableOverflow[0].classList.add('scrollbar')
  }
