import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    errorAlert: null
  },
  getters: {
    errorAlert: state => state.errorAlert
  },
  mutations: {
    set (state, { type, data }) {
      state[type] = data
    }
  },
  actions: {
    set ({ commit }, { type, data }) {
      commit('set', { type: type, data: data })
    }
  }
})
