  <v-container class="home" fluid fill-height>
    <v-layout align-center>
      <v-flex xs1>
        <div class="text-xs-center">
          <v-btn fab dark :small="active !== 'M5'" color="primary" @click="toQuotes('M5')">
            M5
          </v-btn>
          <v-btn fab dark :small="active !== 'H1'" color="primary" @click="toQuotes('H1')">
            H1<br>
          </v-btn>
          <v-btn fab dark :small="active !== 'H4'" color="primary" @click="toQuotes('H4')">
            H4
          </v-btn>
          <v-btn fab dark :small="active !== 'D1'" color="primary" @click="toQuotes('D1')">
            D1
          </v-btn>
        </div>
      </v-flex>
      <v-flex xs11>
        <v-data-table
          :headers="headers"
          :items="coins"
          :loading="loading"
          :disable-initial-sort="true"
          :rows-per-page-items="[10, 15]"
          class="coins-table scrollbar"
          >
          <template v-slot:headers="props">
            <tr>
              <th :key="e.id " v-for="e in props.headers ">
                {{e.text}}
              </th>
            </tr>
