                  </p>
                </div>
                <div v-else>
                  N/D
                </div>
              </td>
              <td class="text-xs-right">
                <div class="mv-table-box" v-if="props.item.lwmas[1] !== null">
                  <p class="mv-info">
                  value : {{ props.item.lwmas[1].val }} <br>
                  last candle : {{ props.item.lwmas[1].last_candle }}
                  </p>
                </div>
                <div v-else>
                  N/D
                </div>
              </td>
              <td class="text-xs-right">
                <div class="mv-table-box" v-if="props.item.lwmas[2] !== null">
                  <p class="mv-info">
                  value : {{ props.item.lwmas[2].val }} <br>
                  last candle : {{ props.item.lwmas[2].last_candle }}
                  </p>
                </div>
                <div v-else>
                  N/D
                </div>
              </td>
              <td class="text-xs-right" >
                <div class="mv-table-box" v-if="props.item.lwmas[3] !== null">
                  <p class="mv-info">
                  value : {{ props.item.lwmas[3].val }} <br>
                  last candle : {{ props.item.lwmas[3].last_candle }}
                  </p>
                </div>
                <div v-else>
                  N/D
                </div>
              </td>
              <td class="text-xs-right">
                <div class="mv-table-box">
                  <p class="mv-info">
                  down : {{ props.item.bb.down === null ? 'N/D' : props.item.bb.down }} <br>
                  up : {{ props.item.bb.up === null ? "N/D" : props.item.bb.up }} <br>
                  </p>
                </div>
              </td>

              <td class="text-xs-right">{{ props.item.atr === null ? 'N/D' : props.item.atr }}</td>
              <td class="text-xs-right">
                <div class="mv-table-box">
                  <p class="mv-info">
                  down : {{ props.item.dc.down === null ? 'N/D' : props.item.dc.down }} <br>
                  up : {{ props.item.dc.up  === null ? 'N/D' : props.item.dc.up }} <br>
                  </p>
                </div>
              </td>
              <td class="text-xs-right">{{ props.item.bbp === null ? 'N/D' : props.item.bbp }}</td>
              <td class="text-xs-right">{{ props.item.adx === null ? 'N/D' : props.item.adx }}</td>
              <td class="text-xs-right">{{ props.item.rsi === null ? 'N/D' : props.item.rsi }}</td>
              <td class="text-xs-right">{{ props.item.macd === null ? 'N/D' : props.item.macd }}</td>
              <td class="text-xs-right">{{ props.item.so === null ? 'N/D' : props.item.so }}</td>
              <td class="text-xs-right">
