<script>
// @ is an alias to /src

export default {
  name: 'home',
  data () {
    return {
      active: 'M5',
      loading: true,
      coins: [],
      headers: [
        { id: 1, text: 'Name', sortable: false },
        { id: 2, text: 'Moving Averages(14)', sortable: false },
        { id: 3, text: 'Moving Averages(55)', sortable: false },
        { id: 4, text: 'Moving Averages(120)', sortable: false },
        { id: 5, text: 'Moving Averages(240)', sortable: false },
        { id: 6, text: 'Bollinger Bands', sortable: false },
        { id: 7, text: 'ATR(14)', sortable: false },
        { id: 8, text: 'Donchian Channel', sortable: false },
        { id: 9, text: 'Bull Bear Power', sortable: false },
        { id: 10, text: 'Average Directional Index', sortable: false },
        { id: 11, text: 'Relative Strength Index', sortable: false },
        { id: 12, text: 'MACD', sortable: false },
        { id: 13, text: 'Stochastic Oscillator', sortable: false },
        { id: 14, text: 'Awesome Oscillator', sortable: false }
      ]
    }
  },
  computed: {
  },
  methods: {
    getData (period) {
      this.coins = []
      this.active = period
      this.$api.get('/quotes/' + period).then((res) => {
        const data = res.data.data
        this.coins = data.crypto_currencies
        console.log(this.coins)
        this.loading = false
        this.$store.commit('set', { type: 'errorAlert', data: { type: 'success', text: 'Data updated' } })
      }).catch((e) => {
        this.$store.commit('set', { type: 'errorAlert', data: { type: 'error', text: e } })
      })
    },
    toQuotes (period) {
      console.log(period)
      this.getData(period)
    }
  },
  created () {
    this.active = this.$route.params.period
    this.getData(this.$route.params.period)
  },
  mounted () {
    console.log('mounted')
    let tableOverflow = document.getElementsByClassName('v-table__overflow')
    tableOverflow[0].classList.add('scrollbar')
  },
  updated () {
  }
}
</script>
