<template>
  <div id="tradingViewId" class="tradingview-widget-container">
    <div id="tradingview_box"></div>
  </div>
</template>
<script>
export default {
  props: {
    coin: {
      type: String,
      default: 'BTC'
    },
    period: {
      type: String,
      default: 'M5'
    }
  },
  data () {
    return {
      graf_period: 'M5'
    }
  },
  methods: {
    getTradingView () {
      this.grafPeriod()
      return new TradingView.widget(
        {
          'autosize': true,
          'symbol': this.coin + 'USD',
          'interval': this.graf_period,
          'height': 400,
          'timezone': 'Etc/UTC',
          'theme': 'Light',
          'style': '1',
          'locale': 'en',
          'toolbar_bg': '#f1f3f6',
          'enable_publishing': false,
          'allow_symbol_change': true,
          'container_id': 'tradingview_box'
        }
      )
    },
    grafPeriod () {
      switch (this.period) {
        case 'M5':
          this.graf_period = '5'
          break
        case 'H1':
          this.graf_period = '60'
          break
        case 'H4':
          this.graf_period = '240'
          break
        case 'D1':
          this.graf_period = 'D'
          break
      }
      console.log(this.graf_period)
    }
  },
  computed: {
  },
  watch: {
    coin: function () {
      console.log('tradingview coin updated', this.coin)
      this.getTradingView()
    },
    period: function () {
      console.log('tradingview period updated', this.coin)
      this.getTradingView()
    }
  },
  created () {
  },
  mounted () {
    this.getTradingView()
    let style = document.querySelectorAll('body style')
    if (style.length > 1) {
      style[0].parentNode.removeChild(style[0])
    }
  },
  beforeDestroy () {
    console.log('destroy tradingView')
  }
}
</script>
<style lang="scss">
.tradingview-widget-container {
  min-height: 70vh;
  position:relative;
  #tradingview_box{
    position:absolute;
    height:100%;
    width: 100%;
    top: 0px;
    left: 0px;
  }

}
</style>
